.container {
    margin: auto;
    width: 80%;
    padding: 20px;
  }

  .slider {
    width: 100%;
    margin: auto;
    padding: 20px;
    
  }

  .card{
    text-align: center;    
    height: 20vh;
    width: 20vw;
    padding: 50px;
    background-color: #f0f0f0;
    overflow: auto;
    border-radius: 15px;
    box-shadow: 0px 0px 5px 0px rgba(0, 0, 0, 0.3);
    font-size: 24px;
  }
  
  .slick-prev:before,
  .slick-next:before {
    color: black;
  }
  
  .city {
    text-align: center;    
    height: 20vh;
    margin:auto;
    border-radius: 15px;
    box-shadow: 0px 0px 5px 0px rgba(0, 0, 0, 0.3);
    padding-top: 50px;
  }

  .city h3 {
    margin-top: 0;
  }
  
  .about {
    text-align: center;
    margin: 40px 0;
  }
  
  .about h2 {
    margin-bottom: 20px;
  }
  
  .attraction {
    text-align: center;
    padding: 40px;
    background-color: #f2f2f2;
    height: 20vh;
  }
  
  .attraction h3 p {
    margin-top: 0;
    
  }
  
  .attraction img {
    height: 300px;
    width: 400px;
    
    margin: auto;
  }

  p{
    width: 80%;
    margin: auto;
  }
  
  .thing-to-do {
    text-align: center;
    padding: 40px;
    background-color: #f2f2f2;
    height: 20vh;
  }
  
  .thing-to-do h3 {
    margin-top: 0;
  }
  
  .thing-to-do img {
    height: 300px;
    width: 400px;
    
    margin: auto;
  }
  
  .event {
    text-align: center;
    padding: 40px;
    background-color: #f2f2f2;
    height: 20vh;
  }
  
  .event h3 {
    margin-top: 0;
  }
  
  .event img {
    height: 300px;
    width: 400px;
    
    margin: auto;
  }
  
  .facts {
    text-align: center;
    margin: 40px 0;
  }
  
  .facts h2 {
    margin-bottom: 20px;
  }
  
  .facts ul {
    list-style: none;
    margin: 0;
    padding: 0;
  }
  
  .facts li {
    font-size: 20px;
    line-height: 1.5;
    margin-bottom: 10px;
  }
  