.CardCarousel {
    position: relative;
    overflow: hidden;
  }
  
  .CardCarousel-slider {
    display: flex;
    transition: transform 0.3s ease-in-out;
    /* justify-content: space-between; */
    
  }
  
  .CardCarousel-card {
    /* flex-shrink: 0; */
    width: 33.33%;
    height: 400px;
    padding: 10px;
    box-shadow: rgb(38, 57, 77) 0px 20px 30px -10px;
        margin-left: 10px;
  }
  
  .CardCarousel-card img {
    width: 350px;
    height:300px;
  }
  
  .CardCarousel-btn {
    position: absolute;
    top: 50%;
    transform: translateY(-50%);
    border: none;
    background-color: transparent;
    color: black;
    font-size: 2em;
    cursor: pointer;
    transition: opacity 0.2s ease-in-out;
    z-index: 1;
  }
  
  .CardCarousel-btn:hover {
    opacity: 0.7;
  }
  
  .CardCarousel-btn--prev {
    left: 0;
  }
  
  .CardCarousel-btn--next {
    right: 0;
  }