.hero{
  /* position:absolute; */
  top:0;
  left:0;
  height: 100vh;
  width: 100vw;
  background: url(.././images/cover.avif) no-repeat center center; 
-webkit-background-size: cover;
-moz-background-size: cover;
-o-background-size: cover;
background-size: cover;

}

.title{
  position: relative;
  font-size: 11rem;
  font-weight: 300;
  font-family: 'Marcellus SC', serif;
  letter-spacing: .5rem;
  display: flex;
  top:4.7rem;
  align-items: center;
  justify-content: center;
}

.box-slogan{
  position: relative;
  display: flex;
  margin: auto;
  justify-content: center;
  align-items: center;
  width: 60rem;
  height: 7.6rem;
  top: 8.4rem; 
  text-align: center;
  font-family: 'Roboto', sans-serif;
  font-size: 1.2rem;
  font-weight:500;
  letter-spacing: .1rem;
  line-height: 2rem; 
  background: rgba(255, 255, 255, 0.667);
  border-radius: 1.2rem;
}


