.jammu-container {
    text-align: left;
    padding: 20px;
    /* background: url('https://media.istockphoto.com/id/175590369/photo/pebble-on-beach.jpg?b=1&s=170667a&w=0&k=20&c=M7qj0XAZKjGSurGFcMYlGoA_1AjqLB_SObw6qMBL49Q='); */
    background-size: cover;
  }
  
  .image-container {
    display: flex;
    justify-content: center;
    align-items: center;
    margin-bottom: 20px;
  }
  
  .image-container img {
    width: 100%;
    height: auto;
  }
  
  .content-container {
    margin-bottom: 20px;
  }
  
  .content-container h1 {
    font-size: 28px;
    margin-bottom: 10px;
  }
  
  .description-container {
    margin-bottom: 20px;
  }
  
  .description-container h2 {
    font-size: 24px;
    margin-bottom: 10px;
  }
  
  .description-container p {
    font-size: 16px;
  }
  
  .must-visit-container {
    margin-bottom: 20px;
  }
  
  .must-visit-container h2 {
    font-size: 24px;
    margin-bottom: 10px;
  }
  
  .must-visit-container ul {
    list-style-type: none;
    padding: 0;
  }
  
  .must-visit-container li {
    font-size: 16px;
    margin-bottom: 5px;
  }
  
  .must-visit-container li:before {
    content: ' ';
    margin-right: 5px;
  }
  