.CardCarousel {
    position: relative;
    overflow: hidden;
  }
  
  .CardCarousel-slider {
    display: flex;
    transition: transform 0.3s ease-in-out;
  }
  
  .CardCarousel-card {
    flex-shrink: 0;
    width: 33.33%;
    padding: 10px;
  }
  
  .CardCarousel-card img {
    max-width: 100%;
  }
  
  .CardCarousel-btn {
    position: absolute;
    top: 50%;
    transform: translateY(-50%);
    border: none;
    background-color: transparent;
    color: black;
    font-size: 2em;
    cursor: pointer;
    transition: opacity 0.2s ease-in-out;
    z-index: 1;
  }
  
  .CardCarousel-btn:hover {
    opacity: 0.7;
  }
  
  .CardCarousel-btn--prev {
    left: 0;
    margin-left: 14px;
  }
  
  .CardCarousel-btn--next {
    right: 0;
  }