.footer-container {
    background-color: #2f2f2f;
    color: #ffffff;
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 30px;
    height: 400px;
  }
  
  .footer-column {
    display: flex;
    flex-direction: column;
    gap: 20px;
  }
  
  .heritage-india {
    font-size: 2.5rem;
    font-weight: bold;
    font-family: Arial, sans-serif;
    margin-bottom: 10px;
  }
  
  .address p {
    margin: 0;
    font-size: 1.2rem;
    line-height: 1.4;
    font-family: Arial, sans-serif;
  }
  
  .social-media a {
    margin-right: 15px;
    color: #ffffff;
    font-size: 2.5rem;
  }
  
  .social-media a:hover {
    color: #ffc107;
  }
  