.image-carousel {
    position: relative;
  }
  
  .glider-slide {
    height: auto;
  }
  
  .dots {
    margin-top: 10px;
    text-align: center;
  }
  
  .image-container {
    position: relative;
    height: 300px; /* Adjust the height as per your requirement */
    overflow: hidden;
  }
  
  .image-container img {
    width: 100%;
    height: 100%;
    object-fit: cover;
  }
  
  .glider-prev,
  .glider-next {
    position: absolute;
    top: 50%;
    transform: translateY(-50%);
    border: none;
    background: none;
    font-size: 24px;
    cursor: pointer;
    color: #000;
    transition: color 0.3s;
    z-index: 1;
  }
  
  .glider-prev {
    left: 10px; /* Adjust the distance from the left side */
  }
  
  .glider-next {
    right: 10px; /* Adjust the distance from the right side */
  }
  
  .glider-prev:hover,
  .glider-next:hover {
    color: #333;
  }
  